@import "assets/common/styles/sass/variables";


body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    line-height: 1.6;
    background-color: $colorApp;
    color: #8c8b8b;

}


*,
*:before,
*:after {
    box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

p {
    margin: 0 0 10px;
}

.app-container {
    display: grid;
    grid-template-areas:
    "nav content contacts";
    grid-template-rows: auto 1fr;
    grid-template-columns: 1fr 2fr 1fr;
    grid-gap: 15px;
    height: 100%;
    //max-width: 12000px;
    margin: 0 auto;
    padding: 0 15px;
}

.app-content {
    display: grid;
    grid-area: content;

}

.darkMode{
    background-color: $colorApp;
    color: $textColor;
}
